const request = require('../utils/request')

// 
function getWeather(params) {
  return request({
    url: 'https://restapi.amap.com/v3/weather/weatherInfo',
    method: 'get',
    responseType: 'text', // 
    params
  })
}

// 
function getIpCode(params) {
  return request({
    url: 'https://restapi.amap.com/v3/ip',
    method: 'get',
    responseType: 'text', // 
    params
  })
}



module.exports = {
  getWeather,
  getIpCode
}